import React from 'react';

export default class ScrollButton extends React.Component{
    constructor(props){
        super(props)
    }

    render() {
        if (this.props.up){
            return(
                <a className={"ScrollButton"}>
                    {this.props.title}
                </a>)
        }
        return (<a className={"ScrollButton"}>
            {this.props.title}
        </a>)
    }
}
